<template>
    <template v-if="readonly">
        {{ model }}
    </template>
    <template v-else>
        <div class="input-group">
            <span class="input-group-text" v-if="prefix">
                <span class="text-pink" v-if="required">*&nbsp;</span>
                {{ prefix }}
            </span>
            <input class="form-control" :placeholder="placeholder" :type="type" v-bind="$attrs" v-model="model" />
        </div>
        <div class="invalid mt-0" v-if="error">{{ error }}</div>
    </template>
</template>

<script setup>
    const model = defineModel();

    defineOptions({ inheritAttrs: false });

    defineProps({
        error: { type: String },
        placeholder: { default: "請輸入", type: String },
        prefix: { type: String },
        readonly: { default: false, type: Boolean },
        required: { default: false, type: Boolean },
        type: { default: "text", type: String }
    });
</script>
